<template>
	<view>
		<scroll-view class="page" :style="{height : scrollH + 'px'}" scroll-y="true">
			<view class="top">
				<image @click="showbig_top_top(imagepath)" class="topimage" :src="imagepath" mode="aspectFill"></image>
			</view>
			<view class="daan_title">
				<image class="wrong_chakan" src="../../static/wrong_daan.png"></image>
				<view class="daan">答案</view>
			</view>

			<view v-for="(item,index) in answer_image_group" class="group">
				<image @click="showbig_top(answer_image_group)" class="answer_image" :src="item" mode="aspectFill"></image>
				<image @click="delete_wrong(index)" class="wrong_image_delete" src="../../static/wrong_image_delete.png"></image>
			</view>
			<template v-if="answer_image_group.length != 3">
			<view class="answer" @click="addimage">
				<image class="wrong_add" src="../../static/wrong_add.png" mode="aspectFill"></image>
				<template v-if="answer_image_group.length > 0">
					<view class="add_text">继续添加</view>
				</template>
				<template v-else>
					<view class="add_text">作答过程或答案可以存入哦</view>
				</template>
			</view>
			</template>
			<view class="daan_title">
				<image class="wrong_chakan" src="../../static/wrong_beizhu.png"></image>
				<view class="daan">备注</view>
			</view>
			<view class="beizhu">
				<textarea @input='textinputing' class="beizhu_textarea" placeholder="好记性不如烂笔头,想到什么快记下来吧"></textarea>
			</view>

		</scroll-view>
		<view class="bottom">
			<view @click="submit" class="cuotiben">存入错题本</view>
		</view>
	</view>
</template>

<script>
	import qiniuUploader from '../../../qiniuUploader.js';
	const app = getApp();
	export default {
		data() {
			return {
				imagepath: '',
				answer_image_group: [],
				scrollH: 0,
				beizhutext: '',
				QiNiutoken: '',
				topicUrl: '',
				Daan_image: []
			}
		},
		onLoad(option) {
			uni.getSystemInfo({
				success: (res) => {
					this.scrollH = res.windowHeight - 60;
				}
			});
			var that = this;
			let gData = app.globalData;
			var wrong_topic = 'UNIAPP_topic' + that.getTime() + 'wrong_topic.png';
			setTimeout(() => {
				uni.request({
					url: gData.site_url + 'Upload.GetQiniuToken',
					method: 'POST',
					data: {
						'uid': getApp().globalData.userinfo.id,
						'token': getApp().globalData.userinfo.token
					},
					success: res => {
						uni.hideLoading();
						if (res.data.data.code == 0) {
							that.QiNiutoken = that.decypt(res.data.data.info[0].token);
							that.imagepath = option.imagepath;
							qiniuUploader.upload(that.imagepath, res => {
								console.log('上传图-成功');
								console.log(res);
								that.topicUrl = res.imageURL;
							}, error => {}, {
								region: 'ECN',
								domain: getApp().globalData.qiniuimageurl,
								key: wrong_topic,
								uptoken: that.QiNiutoken,
							});
						}
					}
				});
			}, 0);

		},
		methods: {
			showbig_top_top(image) {
				let imgsArray = [];
				imgsArray.push(image);
				uni.previewImage({
					current: 0,
					urls: imgsArray,
					loop: false
				});
			},
			showbig_top(imagegroup) {
				let imgsArray = [];
				for (let i = 0; i < imagegroup.length; i++) {
					imgsArray.push(imagegroup[i]);
				}
				uni.previewImage({
					current: 0,
					urls: imgsArray,
					loop: false
				});
			},
			submit() {
				uni.showLoading({
					title: '正在提交...',
					icon: 'none'
				});
				console.log('提交');
				let gData = app.globalData;
				if (this.answer_image_group.length == 0) {
					uni.showToast({
						title: '请添加答案',
						icon: 'none'
					});
					return;
				}
				var that = this;
				let num = 0;
				for (let i = 0; i < that.answer_image_group.length; i++) {
					num += 1;
					var name = 'UNIAPP' + that.getTime() + i + 'answer.png';
					var path = that.answer_image_group[i];
					setTimeout(()=>{
							qiniuUploader.upload(path, res => {
								console.log('上传答案图-成功');
								console.log(res);
								that.Daan_image.push(res.imageURL);
								if (num == that.answer_image_group.length) {
									uni.hideLoading();
									that.Wrongbook_Add();
								}

							}, error => {
								uni.hideLoading();
								uni.showToast({
									title: '上传失败，请重试',
									icon: 'none'
								});
								console.log('上传失败');
								console.log(res);
							}, {
								region: 'ECN',
								domain: getApp().globalData.qiniuimageurl,
								key: name,
								uptoken: that.QiNiutoken,
							});
						},300);
				}
			},
			Wrongbook_Add() {

				var that = this;
				let gData = app.globalData;
				var jsonstr = JSON.stringify(that.Daan_image);
				uni.request({
					url: gData.site_url + 'Wrongbook.Add',
					method: 'GET',
					data: {
						'uid': getApp().globalData.userinfo.id,
						'token': getApp().globalData.userinfo.token,
						'content': that.topicUrl,
						'answer': jsonstr,
						'des': that.beizhutext
					},
					success: res => {
						console.log(res);
						uni.showToast({
							title: res.data.data.msg,
							icon: 'none'
						});

						if (res.data.data.code == 0) {
							setTimeout(() => {
								uni.navigateBack({
									delta: 1
								});
							});

						}

					},
				});

			},
			textinputing(event) {
				this.beizhutext = event.detail.value;
			},
			delete_wrong(index) {
				this.answer_image_group.splice(index, 1);
			},
			getTime() {
				let yy = new Date().getFullYear();
				let mm = new Date().getMonth() + 1;
				let dd = new Date().getDate();
				let hh = new Date().getHours();
				let mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes();
				let ss = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds();
				return yy + mm + dd + hh + mf + ss;
			},
			addimage() {
				if (this.answer_image_group.length == 3) {
					uni.showToast({
						title: '最多添加三张',
						icon: 'none'
					});
					return;
				}
				console.log('打开相册');
				var that = this;
				uni.chooseImage({
					count: 1,
					success(res) {
						console.log(res.tempFilePaths[0]);
						var path = res.tempFilePaths[0];
						that.answer_image_group.push(path);
					}
				});
			},
			decypt(code) {
				var newcode = '';
				var str = '1ecxXyLRB.COdrAi:q09Z62ash-QGn8VFNIlb=fM/D74WjS_EUzYuw?HmTPvkJ3otK5gp&';
				for (var i = 0; i < code.length; i++) {
					var codeIteam = code[i];
					for (var j = 0; j < str.length; j++) {
						var stringIteam = str[j];
						if (codeIteam == stringIteam) {
							if (j == 0) {
								newcode += str[str.length - 1];
							} else {
								newcode += str[j - 1];
							}
						}
					}
				}
				return newcode;
			}
		}
	}
</script>

<style>
	.wrong_image_delete {
		width: 50rpx;
		height: 50rpx;
		position: absolute;
		margin-top: 40rpx;
		right: 70rpx;
	}

	.top {
		width: 100%;
	}

	.group {
		width: 100%;
		height: 330rpx;
		position: relative;
	}

	.answer_image {
		height: 300rpx;
		width: 90%;
		border-radius: 15rpx;
		margin-left: calc(5%);
		margin-top: 20rpx;
	}

	.add_text {
		width: 100%;
		height: 50rpx;
		position: absolute;
		margin-top: 120rpx;
		text-align: center;
		color: #666666;
		font-size: small;

	}

	.wrong_add {
		width: 50rpx;
		height: 50rpx;
		position: absolute;
		margin-top: 60rpx;
		margin-left: calc(50% - 20px);
	}

	.page {
		width: 100%;

	}

	.answer {
		height: 200rpx;
		background-color: #EBEEF5;
		margin-top: 20rpx;
		margin-left: 40rpx;
		margin-right: 40rpx;
		border-radius: 8rpx;
		position: relative;
	}

	.beizhu {
		height: 200rpx;
		background-color: #EBEEF5;
		margin-top: 20rpx;
		margin-left: 40rpx;
		margin-right: 40rpx;
		border-radius: 8rpx;
	}

	.beizhu_textarea {
		padding-left: 10rpx;
		padding-right: 10rpx;
		padding-top: 10rpx;
		width: 100%;
		height: 200rpx;
		font-size: small;
		border-radius: 8rpx;
	}

	.wrong_chakan {
		width: 30rpx;
		height: 30rpx;
		margin-top: 10rpx;
		margin-left: 10rpx;

	}

	.daan {
		margin-left: 10rpx;
	}

	.daan_title {
		display: flex;
		flex-direction: row;
		margin-top: 20rpx;
		margin-left: 30rpx;
	}

	.cuotiben {
		color: #FFFFFF;
		font-weight: bold;
		background-color: #64D3AD;
		height: 60rpx;
		text-align: center;
		padding-top: 10rpx;
		margin-left: 40rpx;
		margin-right: 40rpx;
		border-radius: 8rpx;
	}

	.bottom {
		width: 100%;
		height: 100rpx;
		bottom: 0rpx;
		position: absolute;
		background-color: #FFFFFF;
	}

	.topimage {
		width: 100%;
		height: 400rpx;
	}
</style>
